import moment from "moment";

const diasDiff = (fechaOrigen) => {
  const currentDate = moment().set({
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
    year: 0,
  });
  const dateCelebracion = moment(fechaOrigen);
  const dateCelebracionDia = moment(dateCelebracion).set({
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
    year: 0,
  });
  return dateCelebracionDia.diff(currentDate, "days");
};

export const fechaDiffAniversario = (fechaOrigen) => {
  const dias = diasDiff(fechaOrigen);

  if (dias < 0) {
    return {
      dias,
      msg: "Ya celebraron su aniversario",
    };
  } else if (dias === 0) {
    return {
      dias,
      msg: "Hoy es su aniversario",
    };
  } else {
    return {
      dias,
      msg: `Faltan ${dias} das para su aniversario`,
    };
  }
};

