import { types } from "../../types/types";

export const showLoading = (dispatch) => {
  dispatch({
    type: types.showLoading,
  });
};

export const hideLoading = (dispatch) => {
  dispatch({
    type: types.hideLoading,
  });
};

export const showLoadingCalendar = (dispatch) => {
  dispatch({
    type: types.showLoadingCalendar,
  });
};

export const hideLoadingCalendar = (dispatch) => {
  dispatch({
    type: types.hideLoadingCalendar,
  });
};

export const appLimpia = (dispatch) => {
  dispatch({
    type: types.resetState,
  });
};
